"use client";

import { useCallback, useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";

import { EmptyState } from "@/components/EmptyState";
import { Header } from "@/components/ui/Header";
import { CosmoLoader } from "@/components/ui/CosmoLoader";
import { VoiceCreateModal } from "./components/VoiceCreateModal";
import { VoiceGrid } from "./components/VoiceGrid";

interface VoiceRow {
  id?: string;
  uid: string;
  name: string;
  url: string;
  created_at?: string;
  description?: string | null;
}

export default function VoicesPage() {
  const router = useRouter();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isFetchingVoices, setIsFetchingVoices] = useState(true);
  const [voices, setVoices] = useState<VoiceRow[]>([]);
  const [currentUserId, setCurrentUserId] = useState<string | null>(null);

  const fetchVoices = useCallback(async () => {
    setIsFetchingVoices(true);

      const {
        data: { user },
      } = await supabase.auth.getUser();

      if (!user) {
        router.push("/");
      setCurrentUserId(null);
      setVoices([]);
      setIsFetchingVoices(false);
      return;
    }

    setCurrentUserId(user.id);

    const { data, error } = await supabase
      .from("voices")
      .select("*")
      .eq("uid", user.id)
      .order("created_at", { ascending: false });

    if (error) {
      console.error("[voices] –û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –≥–æ–ª–æ—Å–æ–≤:", error);
      setVoices([]);
    } else {
      console.log("[voices] –ó–∞–≥—Ä—É–∂–µ–Ω–æ –≥–æ–ª–æ—Å–æ–≤:", data?.length ?? 0, data);
      // –§–∏–ª—å—Ç—Ä—É–µ–º —Ç–æ–ª—å–∫–æ –≥–æ–ª–æ—Å–∞ —Å –≤–∞–ª–∏–¥–Ω—ã–º url
      const validVoices = (data ?? []).filter((voice) => voice.url && voice.name);
      console.log("[voices] –í–∞–ª–∏–¥–Ω—ã—Ö –≥–æ–ª–æ—Å–æ–≤:", validVoices.length);
      setVoices(validVoices);
    }
    setIsFetchingVoices(false);
  }, [router]);

  useEffect(() => {
    fetchVoices();
  }, [fetchVoices]);

  return (
    <div className="min-h-screen bg-white">
      <Header />
      <div className="mx-auto flex w-full max-w-[1350px] items-center justify-between px-6 pt-8">
        <h1 className="text-2xl font-bold text-gray-900">–ú–æ–∏ –≥–æ–ª–æ—Å–∞</h1>
        <button
          type="button"
          onClick={() => setIsModalOpen(true)}
          className="min-w-[170px] rounded-lg bg-purple-600 px-4 py-3 text-sm font-semibold text-white transition hover:bg-purple-500"
        >
          –°–æ–∑–¥–∞—Ç—å –≥–æ–ª–æ—Å
        </button>
      </div>
      <main className="mx-auto flex w-full max-w-[1350px] justify-center px-6 py-12">
        {isFetchingVoices ? (
          <EmptyState title="" description="">
            <div className="mt-px flex justify-center">
              <CosmoLoader ariaLabel="–ó–∞–≥—Ä—É–∂–∞–µ–º –≥–æ–ª–æ—Å–∞..." />
            </div>
          </EmptyState>
        ) : voices.length === 0 ? (
        <EmptyState />
        ) : (
          <VoiceGrid
            voices={voices}
            currentUserId={currentUserId}
            onVoicesChanged={fetchVoices}
          />
        )}
      </main>
      <VoiceCreateModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onVoiceCreated={fetchVoices}
      />
    </div>
  );
}